
import { Action } from '@ngrx/store';

export const CREATE = '[request] create';
export const CREATE_SUCCESS = '[request] create success';

export class CreateRequest implements Action {
  readonly type = CREATE;
  public constructor(public payload) { }
}

export class CreateSuccessRequest implements Action {
  readonly type = CREATE_SUCCESS;
  public constructor(public payload) { }
}

export type All = CreateRequest
  | CreateSuccessRequest;



