import { Injectable } from '@angular/core';
import { Effect, Actions, toPayload } from '@ngrx/effects';
import { RoleService } from './../services/role.service';
import { ToastController, App } from 'ionic-angular';
import { Observable } from 'rxjs/Observable';
import { Action } from '@ngrx/store';

import * as roleActions from './../actions/role.actions';
import { Role } from '../models/role.model';

@Injectable()
export class RoleEffects {
  @Effect()
  geRolesList$: Observable<Action> = this.actions$
    .ofType(roleActions.GET_ROLES_SELECT_LIST)
    .map(toPayload)
    .mergeMap(() => this.roleService
      .getListWithPermissions()
      .map((roles: Role[]) => new roleActions.GetRolesSelectListSuccess(roles))
    );

  @Effect()
  createRole$: Observable<Action> = this.actions$
    .ofType(roleActions.CREATE_ROLE)
    .map(toPayload)
    .mergeMap((roleData: Role) => this.roleService
      .createNew(roleData)
      .map(() => new roleActions.CreateRoleSuccess)
    );

  @Effect()
  createRoleSuccess$: Observable<Action> = this.actions$
    .ofType(roleActions.CREATE_ROLE_SUCCESS)
    .map(toPayload)
    .do((payload) => this.app.getActiveNav().push('roles-list-page'))
    .do((payload) => {
      this.toastCtrl.create({
        message: 'Rol creado correctamente.',
        showCloseButton: true,
        closeButtonText: 'OK',
        duration: 5000,
        position: 'top'
      }).present();
    })
    .mergeMap((payload) => []);

  public constructor(
    private app: App,
    private actions$: Actions,
    private roleService: RoleService,
    private toastCtrl: ToastController,
  ) { }
}



