import { Injectable } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/combineLatest';

import { AbstractService } from '../../../abstracts/service.abstract';
import { PermissionService } from './permission.service';
import { Role } from '../models/role.model';
import { Permission } from '../models/permission.model';

@Injectable()
export class RoleService extends AbstractService {
  /**
   * Firebase base key path.
   */
  protected readonly basePath: string = '/roles';

  /**
   * Create new RoleService instance.
   */
  public constructor(
    protected fb: AngularFireDatabase,
    private permissionService: PermissionService,
  ) { super(); }


  /**
   * Creates new role on storage.
   */
  public createNew(role: Role): Observable<any> {
    role.refsArraysToObjects();

    const key = this.fb.app.database().ref().child(this.basePath).push(role).key;
    const data = {};
    data[this.basePath + '/' + key] = role;

    return this.fireBaseUpdate(data).catch(this.handleError);
  }

  /**
   * Get roles list from storage.
   */
  public getList(): Observable<Role[]> {
    return this.fb.list(this.basePath)
      .map((roles: Role[]) => Role.fromJsonList(roles))
      .map(roles => roles.map(role => role.refsObjectsToArrays()));
  }

  /**
   * Get roles list with their attached permissions.
   */
  public getListWithPermissions(): Observable<Role[]> {
    return Observable.combineLatest(
      this.getList(),
      this.permissionService.getList()
    ).map(([roles, permissions]: [Role[], Permission[]]) => this.mergePermissionsOnRoles(roles, permissions));
  }

  /**
   * Merge permissions on roles objects.
   */
  public mergePermissionsOnRoles(roles: Role[], permissions: Permission[]): Role[] {
    return roles.map((role: Role) => {
      permissions
        .filter(permission => role.permission_ids.indexOf(permission.$key) > -1)
        .map(permission => role.permissions.push(permission));

      return role;
    }).reduce((previous, current) => previous.concat(current), []);
  }

}



