import { Action } from '@ngrx/store';
import { User } from '../models/user.model';

export const GET_USERS_LIST = '[USER] get list';
export const GET_USERS_LIST_SUCCESS = '[USER] get list success';
export const CREATE_USER = '[USER] create new user';
export const CREATE_USER_SUCCESS = '[USER] create new user success';

export class GetUsersList implements Action {
  public readonly type = GET_USERS_LIST;
  public constructor(public payload = null) { }
}

export class GetUsersListSuccess implements Action {
  public readonly type = GET_USERS_LIST_SUCCESS;
  public constructor(public payload: User[]) { }
}

export class CreateUser implements Action {
  public readonly type = CREATE_USER;
  public constructor(public payload: User) { }
}

export class CreateUserSuccess implements Action {
  public readonly type = CREATE_USER_SUCCESS;
  public constructor(public payload: any = null) { }
}

export type All
  = GetUsersList
  | GetUsersListSuccess
  | CreateUser
  | CreateUserSuccess;



