import { Action } from '@ngrx/store';
import { Role } from '../models/role.model';

export const CREATE_ROLE = '[ROLE] create new';
export const CREATE_ROLE_SUCCESS = '[ROLE] create new success';
export const GET_ROLES_SELECT_LIST = '[ROLE] get select list';
export const GET_ROLES_SELECT_LIST_SUCCESS = '[ROLE] get select list success';

export class CreateRole implements Action {
  readonly type = CREATE_ROLE;
  public constructor(public payload: Role) { }
}

export class CreateRoleSuccess implements Action {
  readonly type = CREATE_ROLE_SUCCESS;
  public constructor(public payload: any = null) { }
}

export class GetRolesSelectList implements Action {
  readonly type = GET_ROLES_SELECT_LIST;
  public constructor(public payload: any = null) { }
}

export class GetRolesSelectListSuccess implements Action {
  readonly type = GET_ROLES_SELECT_LIST_SUCCESS;
  public constructor(public payload: Role[]) { }
}

export type All
  = CreateRole
  | CreateRoleSuccess
  | GetRolesSelectList
  | GetRolesSelectListSuccess;



