import { Role } from '../../role/models/role.model';
import { AbstractModel } from '../../../abstracts/model.abstract';

export class User extends AbstractModel {
  public get nodeRefsMap(): string[] {
    return ['role_ids'];
  }

  public $key: string;
  public name: string;
  public email: string;
  public role_ids: string[];
  public roles: Role[] = [];

  public static fromJson(data: User): User {
    const key = (data.$key) ? { $key: data.$key } : {};
    return Object.assign(new User, data, key);
  }

  public static fromJsonList(array: User[]): User[] {
    return array.map((data) => User.fromJson(data));
  }

  public joinRolesNames(): string {
    const roles: string[] = [];

    this.roles.forEach(role => roles.push(role.display_name));

    return roles.join(', ');
  }

  /**
   * Check if user has a specific role.
   * @param string role
   */
  public hasRole(role: string): boolean {
    let hasTheRole = false;

    if (this.roles) {
      this.roles.forEach(item => {
        if (item.name === role) {
          hasTheRole = true;
          return;
        }
      });
    }

    return hasTheRole;
  }

  /**
   * Chek if user has at least one of the given roles.
   * @param Array<string> roles
   */
  public hasAnyRole(roles: Array<string>): boolean {
    let hasTheRole = false;

    if (this.roles) {
      this.roles.forEach(item => {
        if (roles.indexOf(item.name) > -1) {
          hasTheRole = true;
          return;
        }
      });
    }


    return hasTheRole;
  }

  /**
   * Check if user has all the given roles.
   * @param Array<roles> roles
   */
  public hasAllRoles(roles: Array<string>): boolean {
    let rolesFound = 0;

    if (this.roles) {
      this.roles.forEach(item => {
        if (roles.indexOf(item.name) > -1) {
          rolesFound++;
        }
      });
    }

    return rolesFound === roles.length;
  }

  /**
   * Check if user has specific permission.
   * @param string permission
   */
  public userCan(permission: string): boolean {
    let hasPermission = false;

    if (this.roles) {
      this.roles.forEach(role => {
        role.permissions.forEach(perm => {
          if (permission === perm.name) {
            hasPermission = true;
            return;
          }
        });

        // permission already found, finish the loop
        if (hasPermission) {
          return;
        }
      });
    }


    return hasPermission;
  }

  /**
   * Check if user has any of the given permissions.
   * @param Array<string> permissions
   */
  public userCanAny(permissions: Array<string>): boolean {
    let hasPermission = false;

    if (this.roles) {
      this.roles.forEach(role => {
        role.permissions.forEach(perm => {
          if (permissions.indexOf(perm.name) > -1) {
            hasPermission = true;
            return;
          }
        });

        // permission already found, finish the loop
        if (hasPermission) {
          return;
        }
      });
    }

    return hasPermission;
  }

  /**
   * Check if user has all the given permissions.
   * @param Array<string> permissions
   */
  public userCanAll(permissions: Array<string>): boolean {
    let permissionsFound = 0;

    if (this.roles) {
      this.roles.forEach(role => {
        role.permissions.forEach(perm => {
          if (permissions.indexOf(perm.name) > -1) {
            permissionsFound++;
          }
        });
      });
    }

    return permissionsFound === permissions.length;
  }

}



