import { createFeatureSelector, createSelector } from '@ngrx/store';

import * as roleActions from './../actions/role.actions';
import * as permissionActions from './../actions/permission.actions';
import { Permission } from './../models/permission.model';
import { Role } from './../models/role.model';

export type Action = roleActions.All | permissionActions.All;

export interface RoleState {
  list: Role[];
}

export interface PermissionState {
  list: Permission[];
}

export interface ModuleState {
  roles: RoleState;
  permissions: PermissionState;
}

export const initialRoleState: RoleState = {
  list: null,
};

export const initialPermissionState: PermissionState = {
  list: null,
};

export function roleReducer(state: RoleState = initialRoleState, action: Action): RoleState {

  switch (action.type) {

    case roleActions.CREATE_ROLE: {
      return { ...state };
    }

    case roleActions.CREATE_ROLE_SUCCESS: {
      return { ...state };
    }

    case roleActions.GET_ROLES_SELECT_LIST_SUCCESS: {
      return { ...state, list: action.payload };
    }

    default: {
      return state;
    }

  }

}

export function permissionReducer(state: PermissionState = initialPermissionState, action: Action): PermissionState {

  switch (action.type) {

    case permissionActions.CREATE_DEFAULT_PERMISSIONS: {
      return { ...state };
    }

    case permissionActions.CREATE_DEFAULT_PERMISSIONS_SUCCESS: {
      return { ...state };
    }

    case permissionActions.GET_PERMISSIONS_LIST: {
      return { ...state };
    }

    case permissionActions.GET_PERMISSIONS_LIST_SUCCESS: {
      return { ...state, list: action.payload };
    }

    default: {
      return state;
    }

  }

}


// roles selectors
export const getRolesModule = createFeatureSelector<ModuleState>('rolesModule');
export const getRolesState = createSelector(getRolesModule, (state: ModuleState) => state.roles);
export const getRolesList = createSelector(getRolesState, (state: RoleState) => state.list);

// permissions selectors
export const getPermissionsModule = createFeatureSelector<ModuleState>('permissionsModule');
export const getPermissionsState = createSelector(getPermissionsModule, (state: ModuleState) => state.permissions);
export const getPermissionsList = createSelector(getPermissionsState, (state: PermissionState) => state.list);



