import { Injectable } from '@angular/core';
import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFireDatabase } from 'angularfire2/database';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import 'rxjs/add/observable/from';

import { ENV } from '@app/config';
import { UserService } from '../../../modules/user/services/user.service';
import { User } from '../../../modules/user/models/user.model';

@Injectable()
export class AuthService {

  public constructor(
    protected fb: AngularFireDatabase,
    private firebaseAuth: AngularFireAuth,
    private userService: UserService,
  ) { }

  public login(): Observable<User> {
    const token: string = this.getToken();
    const userSubject: Subject<User> = new Subject();

    this.firebaseAuth.auth
      .signInWithCustomToken(token)
      .then(
        data => this.userService
          .getUserById(data.uid)
          .subscribe(userData => userSubject.next(userData)),
        error => {
          console.log('login error,', error);
          const errorMsg = '54752073657369c3b36e20686120657870697261646f2e';
          const errorPage = 'error?error=' + errorMsg;
          window.location.href = errorPage;
        }
      );

    return userSubject.asObservable();
  }

  private getToken(): string {
    const host = window.location.hostname;

    if (ENV.PRODUCTION === true ||
      host === 'davicompras.dev' ||
      host === 'davicompras-dev.appspot.com') {

      if (host === 'davicompras.dev' || host === 'davicompras-dev.appspot.com') {
        console.warn('app is on dev mode under ' + host);
      }

      let token: any = document.head.querySelector('meta[name="token"]');
      token = token.content;

      return token;
    }

    return ENV.TEST_USER_TOKEN;
  }

  private strToHex(str: string) {
    //  discuss at: http://locutus.io/php/bin2hex/
    // original by: Kevin van Zonneveld (http://kvz.io)
    // bugfixed by: Onno Marsman (https://twitter.com/onnomarsman)
    // bugfixed by: Linuxworld
    // improved by: ntoniazzi (http://locutus.io/php/bin2hex:361#comment_177616)
    //   example 1: bin2hex('Kev')
    //   returns 1: '4b6576'
    //   example 2: bin2hex(String.fromCharCode(0x00))
    //   returns 2: '00'
    let i;
    let l;
    let o = '';
    let n;

    str += '';

    for (i = 0, l = str.length; i < l; i++) {
      n = str.charCodeAt(i)
        .toString(16);
      o += n.length < 2 ? '0' + n : n;
    }

    return o;
  }

}



