import { Action } from '@ngrx/store';

import { Permission } from '../models/permission.model';
import { PermissionInterface } from './../interfaces/permission.interface';

export const CREATE_DEFAULT_PERMISSIONS = '[PERMISSION] create default';
export const CREATE_DEFAULT_PERMISSIONS_SUCCESS = '[PERMISSION] create default success';
export const GET_PERMISSIONS_LIST = '[PERMISSION] get select list';
export const GET_PERMISSIONS_LIST_SUCCESS = '[PERMISSION] get select list success';

export class CreateDefaultPermissions implements Action {
  public readonly type = CREATE_DEFAULT_PERMISSIONS;
  public constructor(public payload: PermissionInterface[]) { }
}

export class CreateDefaultPermissionsSuccess implements Action {
  public readonly type = CREATE_DEFAULT_PERMISSIONS_SUCCESS;
  public constructor(public payload: any = null) { }
}

export class GetPermissionsList implements Action {
  public readonly type = GET_PERMISSIONS_LIST;
  public constructor(public payload = null) { }
}

export class GetPermissionsListSuccess implements Action {
  public readonly type = GET_PERMISSIONS_LIST_SUCCESS;
  public constructor(public payload: Permission[]) { }
}

export type All
  = CreateDefaultPermissions
  | CreateDefaultPermissionsSuccess
  | GetPermissionsList
  | GetPermissionsListSuccess;



