import { AbstractModel } from '../../../abstracts/model.abstract';
import { PermissionInterface } from './../interfaces/permission.interface';

export class Permission extends AbstractModel implements PermissionInterface {
  protected get nodeRefsMap(): string[] {
    return [];
  };

  public $key: string;
  public name: string;
  public display_name: string;

  public static fromJson(data: Permission): Permission {
    const key = (data.$key) ? { $key: data.$key } : {};
    return Object.assign(new Permission, data, key);
  }

  public static fromJsonList(array: Permission[]): Permission[] {
    return array.map((data) => Permission.fromJson(data));
  }
}



