import { AbstractModel } from '../../../abstracts/model.abstract';
import { Permission } from './permission.model';

export class Role extends AbstractModel {
  protected get nodeRefsMap(): string[] {
    return [
      'permission_ids'
    ];
  }

  public readonly $key: string;
  public name: string;
  public display_name: string;
  public permission_ids: any[];
  public permissions: Permission[] = [];

  public static fromJson(data: Role): Role {
    const key = (data.$key) ? { $key: data.$key } : {};
    return Object.assign(new Role, data, key);
  }

  public static fromJsonList(array: Role[]): Role[] {
    return array.map((data) => Role.fromJson(data));
  }

  public joinPermissionsNames(): string {
    const permissions: string[] = [];

    this.permissions.forEach(permission => permissions.push(permission.display_name));

    return permissions.join(', ');
  }

}



