import { Injectable } from '@angular/core';
import { Effect, Actions, toPayload } from '@ngrx/effects';
import { PermissionService } from './../services/permission.service';
import { ToastController, App } from 'ionic-angular';
import { Observable } from 'rxjs/Observable';
import { Action } from '@ngrx/store';

import * as permissionActions from './../actions/permission.actions';
import { Permission } from '../models/permission.model';

@Injectable()
export class PermissionEffects {
  @Effect()
  getPermissionsList$: Observable<Action> = this.actions$
    .ofType(permissionActions.GET_PERMISSIONS_LIST)
    .map(toPayload)
    .mergeMap(() => this.permissionService
      .getList()
      .map((permissionsList: Permission[]) => {
        return new permissionActions.GetPermissionsListSuccess(permissionsList);
      })
    );

  @Effect()
  createDefaultPermissions$: Observable<Action> = this.actions$
    .ofType(permissionActions.CREATE_DEFAULT_PERMISSIONS)
    .map(toPayload)
    .mergeMap((permissionsList: Permission[]) => this.permissionService
      .createDefault(permissionsList)
      .map(() => new permissionActions.CreateDefaultPermissionsSuccess)
    );

  @Effect()
  createDefaultPermissionsSuccess$: Observable<Action> = this.actions$
    .ofType(permissionActions.CREATE_DEFAULT_PERMISSIONS_SUCCESS)
    .map(toPayload)
    .do((payload) => this.app.getActiveNav().push('role-form-page'))
    .do((payload) => {
      this.toastCtrl.create({
        message: 'Permisos predeterminados creados correctamente.',
        showCloseButton: true,
        closeButtonText: 'OK',
        duration: 5000,
        position: 'top'
      }).present();
    })
    .mergeMap((payload) => []);

  public constructor(
    private app: App,
    private actions$: Actions,
    private permissionService: PermissionService,
    private toastCtrl: ToastController,
  ) { }
}



