import { Store } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

import { NOTIFICATION_CONFIG } from '../../config';
import * as fromRoot from '../../../../app/reducers';
import * as fromUser from '../../../user/reducers/user.reducer';
import * as userActions from '../../../user/actions/user.actions';
import * as notificationActions from './../../actions/notification.actions';
import { NotificationService } from '../../services/notification.service';
import { Notification } from '../../models/notification.model';
import { User } from '../../../user/models/user.model';

@Component({
  selector: 'app-notification-form',
  templateUrl: 'notification-form.component.html'
})
export class NotificationFormComponent implements OnInit {
  /**
   * Users list observable.
   */
  public usersList$: Observable<User[]>;

  /**
   * Notification reactive form.
   */
  public form: FormGroup;

  /**
   * Potification types (success|warning|danger|info).
   */
  public get typeOptions(): any[] {
    return NOTIFICATION_CONFIG.types;
  }

  /**
   * Create new NotificationFormComponent instance.
   */
  public constructor(
    private store: Store<fromRoot.State>,
    private fb: FormBuilder
  ) { }

  public ngOnInit() {
    this.usersList$ = this.store.select(fromUser.getUsersList);
    this.store.dispatch(new userActions.GetUsersList);

    this.form = this.fb.group({
      // TODO: define title length
      title: [null, [Validators.required]],
      // TODO: define body length
      body: [null, [Validators.required]],
      type: [null, Validators.required],
      recipient_ids: [[]],
      sender_id: 1
    });
  }

  /**
   * Submit the notification form.
   */
  public submit() {
    const data = Notification.fromJson(this.form.value);

    this.store.dispatch(new notificationActions.CreateNotification(data));
  }
}



