import { Action } from '@ngrx/store';

export const SETUP_INITIAL_DATABASE = '[SYSTEM] setup initial database';
export const SETUP_INITIAL_DATABASE_SUCCESS = '[SYSTEM] setup initial database success';

export class SetupInitialDatabase implements Action {
  readonly type = SETUP_INITIAL_DATABASE;
  public constructor(public payload = null) { }
}

export class SetupInitialDatabaseSuccess implements Action {
  readonly type = SETUP_INITIAL_DATABASE_SUCCESS;
  public constructor(public payload: string = 'success') { }
}

export type All
  = SetupInitialDatabase
  | SetupInitialDatabaseSuccess;



