import { Component, OnInit, Input } from '@angular/core';

import { Notification } from './../../models/notification.model';
import { NOTIFICATION_CONFIG } from '../../config';

@Component({
  selector: 'app-notification-list-item',
  template: `
    <ion-item *ngIf="notification" [ngClass]="className">
      <ion-icon item-start [name]="icon"></ion-icon>
      <h2>{{ notification.title }} <small>{{ notification.created_at | date:'medium' }}</small> </h2>
      <p>{{ notification.body }}</p>
    </ion-item>
  `
})

export class NotificationListItemComponent implements OnInit {

  @Input()
  public notification: Notification;

  constructor() { }

  ngOnInit() { }

  public get notificatioType() {
    return NOTIFICATION_CONFIG.types
      .filter(type => type.name === this.notification.type)[0]
  }

  public get icon(): string {
    const icon = this.notificatioType;
    return icon ? icon.icon : 'md-megaphone';
  }

  public get className(): string {
    const className = this.notificatioType;
    return className ? className.name : 'info';
  }
}



