import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFireModule } from 'angularfire2';
import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { StoreModule, Store } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
import { IonicApp, IonicErrorHandler, IonicModule, IonicPageModule } from 'ionic-angular';

import * as authActions from '../modules/auth/actions/auth.actions';

import { MyAppComponent } from './app.component';
import { MODULES } from '../modules';
import { ENV } from '@app/config';
import * as fromRoot from './reducers';

@NgModule({
  declarations: [
    MyAppComponent
  ],
  imports: [
    AngularFireDatabaseModule,
    AngularFireModule.initializeApp(ENV.FIREBASE_CONFIG),
    BrowserModule,
    IonicModule.forRoot(MyAppComponent, {}, 
{
  links: [
    { loadChildren: '../modules/auth/pages/landing/landing.page.module#LandingPageComponentModule', name: 'landing-page', segment: 'landing-page', priority: 'low', defaultHistory: [] },
    { loadChildren: '../modules/auth/pages/login/login.page.module#LoginPageComponentModule', name: 'login-page', segment: 'auth/login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../modules/faq/pages/faq/faq.page.module#FaqPageModule', name: 'faqs-list-page', segment: 'faqs', priority: 'low', defaultHistory: [] },
    { loadChildren: '../modules/notification/pages/notification-form/notification-form.page.module#NotificationFormPageModule', name: 'create-notification-page', segment: 'notifications/create', priority: 'low', defaultHistory: ['notifications-list-page'] },
    { loadChildren: '../modules/notification/pages/notifications-list/notifications-list.page.module#NotificationsListPageModule', name: 'notifications-list-page', segment: 'notifications', priority: 'low', defaultHistory: [] },
    { loadChildren: '../modules/requests/pages/requets-list/requests-list.page.module#RequestsListPageModule', name: 'requests-list-page', segment: 'requests', priority: 'low', defaultHistory: [] },
    { loadChildren: '../modules/role/pages/role-form/role-form.page.module#UserFormPageModule', name: 'create-role-page', segment: 'roles/create', priority: 'low', defaultHistory: ['roles-list-page'] },
    { loadChildren: '../modules/role/pages/roles-list/roles-list.page.module#UserFormPageModule', name: 'roles-list-page', segment: 'roles', priority: 'low', defaultHistory: [] },
    { loadChildren: '../modules/user/pages/user-form/user-form.page.module#UserFormPageModule', name: 'create-user-page', segment: 'users/create', priority: 'low', defaultHistory: ['users-list-page'] },
    { loadChildren: '../modules/user/pages/users-list/users-list.page.module#UsersListPageModule', name: 'users-list-page', segment: 'users', priority: 'low', defaultHistory: [] }
  ]
}),
    StoreModule.forRoot(fromRoot.REDUCER_TOKEN),
    EffectsModule.forRoot([]),
    ...MODULES,
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyAppComponent
  ],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    fromRoot.reducerProvider,
  ]
})
export class AppModule {

  public constructor(store: Store<any>) {
    store.dispatch(new authActions.LoginWithToken);
  }

}



