import { Injectable } from '@angular/core';
import { Effect, Actions, toPayload } from '@ngrx/effects';
import { Action } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';
import { ToastController } from 'ionic-angular';
import 'rxjs/add/operator/do';
import { empty } from 'rxjs/observable/empty';

import * as sharedActions from './../actions/shared.actions';
import { SharedService } from './../services/shared.service';

@Injectable()
export class SharedEffects {

  @Effect()
  setupInitialData$: Observable<Action> = this.actions
    .ofType(sharedActions.SETUP_INITIAL_DATABASE)
    .map(toPayload)
    .switchMap(data => this.sharedSrvc
      .setupInitialDatabase()
      .map((res: Response) => new sharedActions.SetupInitialDatabaseSuccess(JSON.stringify(res.body)))
      .catch((error: Response) => {
        this.toastCtrl.create({
          message: 'There was a f****ng error: ' + error.statusText,
          showCloseButton: true,
          closeButtonText: 'OK',
          duration: 5000,
          position: 'top'
        }).present();

        return [];
      })
    );

@Effect()
setupInitialDataSuccess$: Observable < Action > = this.actions
  .ofType(sharedActions.SETUP_INITIAL_DATABASE_SUCCESS)
  .map(toPayload)
  .do(payload => this.toastCtrl.create({
    message: JSON.stringify('Database initialazed'),
    showCloseButton: true,
    closeButtonText: 'OK',
    duration: 5000,
    position: 'top'
  }).present())
  .mergeMap((payload) => []);

  public constructor(
    private actions: Actions,
    private sharedSrvc: SharedService,
    private toastCtrl: ToastController,
  ) { }
}



