import { Component, OnInit } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';

import * as fromRoles from './../../reducers/role.reducer';
import * as roleActions from './../../actions/role.actions';
import { Role } from '../../models/role.model';

@Component({
  selector: 'app-roles-list',
  template: `
    <ion-list col-xs-12>
      <ion-item *ngFor="let role of rolesList$ | async" text-wrap>
        <h2>{{ role.display_name }} <small>{{ role.name }}</small></h2>
        <p>{{ role.permissions.length }} Permissions: <small>{{ role.joinPermissionsNames() }}</small><p>
      </ion-item>
    </ion-list>
    `
})
export class RolesListComponent implements OnInit {
  /**
   * The roles list observable.
   */
  public rolesList$: Observable<Role[]>;

  constructor(
    public navCtrl: NavController,
    private store: Store<fromRoles.ModuleState>,
  ) { }

  ngOnInit() {
    this.rolesList$ = this.store.select(fromRoles.getRolesList);
    this.store.dispatch(new roleActions.GetRolesSelectList);
  }
}



