import { Injectable } from '@angular/core';
import { Actions, Effect, toPayload } from '@ngrx/effects';
import { Action } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/mergeMap';
import 'rxjs/add/operator/do';
import { App } from 'ionic-angular';

import { NotificationService } from '../services/notification.service';
import * as notificationActions from '../actions/notification.actions';
import { Notification } from './../models/notification.model';
import { NavController, ToastController } from 'ionic-angular';

@Injectable()
export class NotificationEffects {
  @Effect()
  getCurrentUserNotifications$: Observable<Action> = this.actions$
    .ofType(notificationActions.GET_CURRENT_USER_NOTIFICATIONS)
    .map(toPayload)
    .mergeMap(payload => this.notificationService
      .currentUserNotifications()
      .map((list: Notification[]) => new notificationActions.GetCurrentUserNotificationsSuccess(list))
    );

  @Effect()
  createNotification$: Observable<Action> = this.actions$
    .ofType(notificationActions.CREATE_NOTIFICATION)
    .map(toPayload)
    .mergeMap((notification: Notification) => this.notificationService
      .createNotification(notification)
      .map(item => new notificationActions.CreateNotificationSuccess(item))
    );

  @Effect()
  createNotificationSuccess$: Observable<Action> = this.actions$
    .ofType(notificationActions.CREATE_NOTIFICATION_SUCCESS)
    .map(toPayload)
    .do((payload) => this.app.getActiveNav().push('notifications-list-page'))
    .do((payload) => {
      this.toastCtrl.create({
        message: 'Notificación Enviada.',
        showCloseButton: true,
        closeButtonText: 'OK',
        duration: 5000,
        position: 'top'
      }).present();
    })
    .mergeMap((payload) => []);

  public constructor(
    private actions$: Actions,
    private notificationService: NotificationService,
    private app: App,
    private toastCtrl: ToastController,
  ) { }
}



