import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { AngularFireAuth } from 'angularfire2/auth';

import { Notification } from './../models/notification.model';
import { AbstractService } from '../../../abstracts/service.abstract';

@Injectable()
export class NotificationService extends AbstractService {

  /**
   * Firebase base key path.
   */
  protected readonly basePath: string = '/notifications';

  /**
   * Create new NotificationService instance.
   */
  public constructor(public fb: AngularFireDatabase) { super(); }

  /**
   * Get current user notifications from storage.
   */
  public currentUserNotifications(limit: number = 20): FirebaseListObservable<Notification[]> {
    const query = { limitToLast: limit };

    // TODO: query only the related user notifications
    return this.fb.list(this.basePath, {
      query: {
        orderByChild: '_created_at_',
        limitToFirst: 5,
      }
    });
  }

  /**
   * Create new notification on storage.
   */
  public createNotification(notification: Notification): Observable<any> {
    notification.refsArraysToObjects();

    const key = this.fb.app.database().ref().child(this.basePath).push(notification).key;
    const data = {};
    data[this.basePath + '/' + key] = notification;

    return this.fireBaseUpdate(data).catch(this.handleError);
  }

}



