import { NgModule } from '@angular/core';
import { RoleSharedModule } from './role.shared.module';
import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';

import { SERVICES } from './services';
import { EFFECTS } from './effects';
import { roleReducer, permissionReducer } from './reducers/role.reducer';

@NgModule({
  imports: [
    RoleSharedModule,
    StoreModule.forFeature('rolesModule', { roles: roleReducer }),
    StoreModule.forFeature('permissionsModule', { permissions: permissionReducer }),
    EffectsModule.forFeature(EFFECTS),
  ],
  exports: [],
  declarations: [],
  providers: [SERVICES],
})
export class RoleModule { }



