import * as userActions from './../actions/user.actions';
import { createFeatureSelector, createSelector } from '@ngrx/store';

import { User } from './../models/user.model';

export type Actions = userActions.All;

export interface ModuleState {
  users: State;
}

export interface State {
  list: User[];
}

export const initialState: State = {
  list: null,
};

export function reducer(state: State = initialState, action: Actions): State {

  switch (action.type) {

    case userActions.CREATE_USER: {
      return { ...state };
    }

    case userActions.CREATE_USER_SUCCESS: {
      return { ...state };
    }

    case userActions.GET_USERS_LIST_SUCCESS: {
      return { ...state, list: action.payload };
    }

    default: {
      return state;
    }

  }

}

// users selectors
export const getUsersModule = createFeatureSelector<ModuleState>('usersModule');
export const getUsersState = createSelector(getUsersModule, (state: ModuleState) => state.users);
export const getUsersList = createSelector(getUsersState, (state: State) => state.list);



