import * as notificationActions from './../actions/notification.actions';
import { createFeatureSelector, createSelector } from '@ngrx/store';

import { Notification } from './../models/notification.model';

export type Action = notificationActions.All;

export interface State {
  list: Notification[];
}

export interface ModuleState {
  notifications: State;
}

export const initialState: State = {
  list: null,
};

export function reducer(state: State = initialState, action: Action): State {

  switch (action.type) {

    case notificationActions.GET_CURRENT_USER_NOTIFICATIONS: {
      return { ...state };
    }

    case notificationActions.GET_CURRENT_USER_NOTIFICATIONS_SUCCESS: {
      return { ...state, list: action.payload };
    }

    default: {
      return state;
    }

  }

}

// notifications selectors
export const getNotificationsModule = createFeatureSelector<ModuleState>('notificationsModule');
export const getNotificationsState = createSelector(getNotificationsModule, (state: ModuleState) => state.notifications);
export const getUserNotifications = createSelector(getNotificationsState, (state: State) => state.list);



