import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Store } from '@ngrx/store';

import * as userActions from './../../actions/user.actions';
import * as fromUser from './../../reducers/user.reducer';
import * as roleActions from './../../../role/actions/role.actions';
import * as fromRole from './../../../role/reducers/role.reducer';
import { Role } from '../../../role/models/role.model';
import { Observable } from 'rxjs/Observable';
import { User } from '../../models/user.model';

@Component({
  selector: 'app-user-form-component',
  templateUrl: 'user-form.component.html'
})
export class UserFormComponent implements OnInit {

  /**
   * The roles list observable.
   */
  public rolesList$: Observable<Role[]>;

  /**
   * The user form.
   */
  public form: FormGroup;

  public constructor(
    private fb: FormBuilder,
    private store: Store<fromRole.ModuleState>
  ) { }

  public ngOnInit() {
    this.rolesList$ = this.store.select(fromRole.getRolesList);
    this.store.dispatch(new roleActions.GetRolesSelectList);

    this.form = this.fb.group({
      fullname: [null, [Validators.required]],
      role_ids: [[], [Validators.required]],
    });
  }

  public submit() {
    const user = User.fromJson(this.form.value);
    this.store.dispatch(new userActions.CreateUser(user));
  }
}



