import { Injectable } from '@angular/core';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { Observable } from 'rxjs/Observable';

import { AbstractService } from '../../../abstracts/service.abstract';
import { Permission } from '../models/permission.model';

@Injectable()
export class PermissionService extends AbstractService {

  /**
   * Firebase base key path.
   */
  protected readonly basePath: string = '/permissions';

  /**
   * Create new RoleService instance.
   */
  public constructor(protected fb: AngularFireDatabase) { super(); }

  /**
   * Get permissions list from storage.
   */
  public getList(): Observable<Permission[]> {
    const query = {
      orderByChild: 'display_name',
    };

    return this.fb.list(this.basePath, { query: query })
      .map(permissions => Permission.fromJsonList(permissions));
  }

  /**
   * Creates new permission on storage.
   */
  public createNew(permission: Permission): Observable<any> {
    const key = this.fb.app.database().ref().child(this.basePath).push(permission).key;
    const data = {};
    data[this.basePath + '/' + key] = permission;

    return this.fireBaseUpdate(data).catch(this.handleError);
  }

  /**
   * Creates a bunch of permissions on storage.
   */
  public createDefault(permissions: Permission[]): Observable<any> {
    const data = {};

    permissions.forEach(permission => {
      const key = this.fb.app.database().ref().child(this.basePath).push(permission).key;
      data[this.basePath + '/' + key] = permission;
    });

    return this.fireBaseUpdate(data).catch(this.handleError);
  }

}



