import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Store } from '@ngrx/store';

import { ROLE_CONFIG } from './../../config';

import * as roleActions from './../../actions/role.actions';
import * as fromRole from './../../reducers/role.reducer';
import * as permissionActions from './../../actions/permission.actions';
import { Permission } from '../../models/permission.model';
import { Observable } from 'rxjs/Observable';
import { Role } from '../../models/role.model';

@Component({
  selector: 'app-role-form-component',
  templateUrl: 'role-form.component.html'
})
export class RoleFormComponent implements OnInit {

  /**
   * Permissions list observable.
   */
  public permissionsList$: Observable<Permission[]>;

  /**
   * Role reacive form.
   */
  public form: FormGroup;

  public constructor(
    private fb: FormBuilder,
    private store: Store<fromRole.ModuleState>
  ) { }

  public ngOnInit() {
    this.permissionsList$ = this.store.select(fromRole.getPermissionsList);
    this.store.dispatch(new permissionActions.GetPermissionsList());

    this.form = this.fb.group({
      name: [null, [Validators.required]],
      display_name: [null, [Validators.required]],
      permission_ids: [[], [Validators.required]],
    });
  }

  public submit() {
    const role: Role = Role.fromJson(this.form.value);
    this.store.dispatch(new roleActions.CreateRole(role));
  }

  public createDefaultPermissions() {
    this.store.dispatch(new permissionActions.CreateDefaultPermissions(ROLE_CONFIG.default_permissions));
  }

}



