import { map } from 'lodash';

export abstract class AbstractModel {
  /**
   * Attrs that are references to Firebase nodes, example:
   * book: { name: 'Foo', author_id: '-Kq3ys' }
   * author_id is a reference to author node, so should be maped to:
   * ['author_id']
   */
  protected abstract get nodeRefsMap(): string[];

  /**
   * Parse all node reference array attrs to objects based on the config given on nodeRefsMap attr.
   */
  public refsArraysToObjects() {
    this.nodeRefsMap.forEach(attr => this.arrayAttrToObject(attr));

    return this;
  }

  /**
   * Transform the given attr from array to object.
   */
  public arrayAttrToObject(attr: string) {
    this[attr] = this[attr].reduce((result, item) => {
      result[item] = true;
      return result;
    }, {});


    return this;
  }

  /**
   * Transform the given attr from object to array.
   */
  public refsObjectsToArrays(): this {
    this.nodeRefsMap.forEach(attr => this.objectAttrToArray(attr));

    return this;
  }

  /**
   * Transform the given attr from object (the keys) to array.
   */
  public objectAttrToArray(attr: string) {
    const data: any[] = [];

    // only add keys to the array when the value is true
    map(this[attr], (value: any, key: any) => {
      return value === true ? data.push(key) : null;
    });

    this[attr] = data;

    return this;
  }

}



