import { Action } from '@ngrx/store';

import { Notification } from './../models/notification.model';

export const GET_CURRENT_USER_NOTIFICATIONS = '[notification] get current user notifications list';
export const GET_CURRENT_USER_NOTIFICATIONS_SUCCESS = '[notification] get current user notifications list success';
export const CREATE_NOTIFICATION = '[notification] create';
export const CREATE_NOTIFICATION_SUCCESS = '[notification] create success';

export class GetCurrentUserNotifications implements Action {
  public readonly type = GET_CURRENT_USER_NOTIFICATIONS;
  public constructor(public payload = null) { }
}

export class GetCurrentUserNotificationsSuccess implements Action {
  public readonly type = GET_CURRENT_USER_NOTIFICATIONS_SUCCESS;
  public constructor(public payload: Notification[] = []) { }
}

export class CreateNotification implements Action {
  public readonly type = CREATE_NOTIFICATION;
  public constructor(public payload: Notification) { }
}

export class CreateNotificationSuccess implements Action {
  public readonly type = CREATE_NOTIFICATION_SUCCESS;
  public constructor(public payload = null) { }
}

export type All
  = GetCurrentUserNotifications
  | GetCurrentUserNotificationsSuccess
  | CreateNotification
  | CreateNotificationSuccess;



