import { Injectable } from '@angular/core';
import { Effect, Actions, toPayload } from '@ngrx/effects';
import { UserService } from './../services/user.service';
import { ToastController, App } from 'ionic-angular';
import { Observable } from 'rxjs/Observable';
import { Action } from '@ngrx/store';
import 'rxjs/add/operator/switchMap';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/mergeMap';
import 'rxjs/add/operator/do';

import * as userActions from './../actions/user.actions';
import { User } from '../models/user.model';

@Injectable()
export class UserEffects {

  @Effect()
  createUser$: Observable<Action> = this.actions$
    .ofType(userActions.CREATE_USER)
    .map(toPayload)
    .switchMap((userData: User) => this.userService
      .createNew(userData)
      .map(() => new userActions.CreateUserSuccess)
    );

  @Effect()
  createUserSuccess$: Observable<Action> = this.actions$
    .ofType(userActions.CREATE_USER_SUCCESS)
    .map(toPayload)
    .do((payload) => this.app.getActiveNav().push('users-list-page'))
    .do((payload) => {
      this.toastCtrl.create({
        message: 'Usuario creado correctamente.',
        showCloseButton: true,
        closeButtonText: 'OK',
        duration: 5000,
        position: 'top'
      }).present();
    })
    .mergeMap((payload) => []);

  @Effect()
  getUsersList$: Observable<Action> = this.actions$
    .ofType(userActions.GET_USERS_LIST)
    .map(toPayload)
    .mergeMap(() => this.userService
      .getListWithRoles()
      .map((users) => new userActions.GetUsersListSuccess(users))
    );

  public constructor(
    private app: App,
    private actions$: Actions,
    private userService: UserService,
    private toastCtrl: ToastController,
  ) { }
}



