import * as requestActions from '../actions/request.actions';
import { createFeatureSelector, createSelector } from '@ngrx/store';

export type Actions = requestActions.All;

interface RequestFeatureModule {
  requests: RequestState
}

export interface RequestState {
  selectedRequest: any;
}

const initialState: RequestState = {
  selectedRequest: null,
};

export function reducer(state: RequestState = initialState, action: Actions): RequestState {

  switch (action.type) {

    case requestActions.CREATE: {
      return state;
    }

    case requestActions.CREATE_SUCCESS: {
      return state;
    }

    default: {
      return state;
    }

  }

}

// selectors
export const getRequestsModule = createFeatureSelector<RequestFeatureModule>('requestsModule');
export const getRequestsState = createSelector(getRequestsModule, (state: RequestFeatureModule) => state.requests);




