import { Component, ViewChild } from '@angular/core';
import { Platform, Nav } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

export interface PageInterface {
  title: string;
  name: string;
  icon: string;
  index?: number;
  isHeader?: boolean;
}

@Component({
  templateUrl: 'app.component.html'
})
export class MyAppComponent {

  @ViewChild(Nav) nav: Nav;

  public rootPage: any = 'landing-page';

  public pages: PageInterface[] = [
    { title: 'Main', name: '', icon: '', isHeader: true },
    { title: 'Solicitudes', name: 'requests-list-page', icon: 'md-filing', },
    { title: 'Notificaciones', name: 'notifications-list-page', icon: 'md-notifications', },
    { title: 'FAQS', name: 'faqs-list-page', icon: 'md-help', },
    { title: 'Config', name: '', icon: '', isHeader: true },
    { title: 'Usuarios', name: 'users-list-page', icon: 'md-contacts', },
    { title: 'Roles', name: 'roles-list-page', icon: 'md-key', },
  ];

  public constructor(
    platform: Platform,
    statusBar: StatusBar,
    splashScreen: SplashScreen
  ) {
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();
    });
  }

  openPage(page: PageInterface) {
    const params = {};

    // If we are already on tabs just change the selected tab
    // don't setRoot again, this maintains the history stack of the
    // tabs even if changing them from the menu
    if (this.nav.getActiveChildNavs() && page.index !== undefined) {
      this.nav.getActiveChildNav().select(page.index);
      // Set the root of the nav with params if it's a tab index
    } else {
      this.nav.setRoot(page.name, params)
        .catch((err: any) => {
          console.log(`Didn't set nav root: ${err}`);
        });
    }

  }

  isActive(page: PageInterface) {
    if (this.nav.getActive() && this.nav.getActive().name === page.name) {
      return 'primary';
    }

    return;
  }

}




