import { NgModule } from '@angular/core';
import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
import { IonicModule } from 'ionic-angular';

import { EFFECTS } from './effects';
import { SERVICES } from './services';
import { reducer } from './reducers/notification.reducer';
import { NotificationSharedModule } from './notification.shared.module';

@NgModule({
  imports: [
    StoreModule.forFeature('notificationsModule', { notifications: reducer }),
    EffectsModule.forFeature(EFFECTS),
    IonicModule,
    NotificationSharedModule,
  ],
  exports: [
  ],
  declarations: [
  ],
  providers: [
    ...SERVICES
  ],
})
export class NotificationsModule { }



