import { User } from '../../../modules/user/models/user.model';
import * as authActions from '../actions/auth.actions';
import { createSelector, createFeatureSelector } from '@ngrx/store';

export type Actions = authActions.All;

export interface AuthFeatureModuleState {
  authState: AuthState
}

export interface AuthState {
  loggedIn: boolean;
  currentUser: User;
}

const initialState: AuthState = {
  loggedIn: false,
  currentUser: null
}

export function reducer(state: AuthState = initialState, action: Actions): AuthState {

  switch (action.type) {

    case authActions.LOGIN_SUCCESS: {
      return { ...state, loggedIn: true, currentUser: action.payload };
    }

    default: {
      return state;
    }

  }

}

export const getAuthModuleState = createFeatureSelector<AuthFeatureModuleState>('authModule');
export const getAuthState = createSelector(getAuthModuleState, (state: AuthFeatureModuleState) => state.authState);
export const getUserIsLoggedIn = createSelector(getAuthState, (state: AuthState) => state.loggedIn);
export const getCurrentAuthUser = createSelector(getAuthState, (state: AuthState) => state.currentUser);



