import { AngularFireDatabase } from 'angularfire2/database';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import { ENV } from '@app/config';

export abstract class AbstractService {

  /**
   * The Firebase database CLI.
   */
  protected abstract fb: AngularFireDatabase;

  /**
   * The Firebase data base path.
   */
  protected abstract basePath: string;

  /**
   * Handle error responses from Firebase.
   */
  protected handleError(error: any) {
    if (ENV.PRODUCTION === false) {
      console.error(typeof this, error);
    }

    return error;
  }

  /**
   * Performs write actions on Firebase.
   */
  protected fireBaseUpdate(data): Observable<any> {
    const subject = new Subject();

    this.fb.app.database().ref().update(data).then(
      value => { subject.next(value); subject.complete(); },
      error => { subject.error(error); subject.complete(); }
    );

    return subject.asObservable();
  }

}



