import { Action } from '@ngrx/store';
import { User } from '../../../modules/user/models/user.model';

export const LOGIN_WITH_TOKEN = '[auth] login with token';
export const LOGIN_SUCCESS = '[auth] login success';

export class LoginWithToken implements Action {
  readonly type = LOGIN_WITH_TOKEN;
  public constructor(public payload = null) { }
}

export class LoginSuccess implements Action {
  readonly type = LOGIN_SUCCESS;
  public constructor(public payload: User) { }
}

export type All = LoginWithToken
  | LoginSuccess;



