import { AbstractModel } from '../../../abstracts/model.abstract';

export class Notification extends AbstractModel {
  public $key: string;
  public title: string;
  public body: string;
  public type: string;
  public sender_id: string;
  public recipient_ids: string[];
  public created_at: number;
  public _created_at_: number;

  public get nodeRefsMap(): string[] {
    return [
      'recipient_ids'
    ];
  }

  public static fromJson(data: Notification): Notification {
    const key = (data.$key) ? { $key: data.$key } : {};
    return Object.assign(new Notification, data, key);
  }

  public static fromJsonList(array: Notification[]): Notification[] {
    return array.map((data) => Notification.fromJson(data));
  }

}



