import { Injectable } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { Observable } from 'rxjs/Observable';

import { AbstractService } from '../../../abstracts/service.abstract';
import { User } from '../models/user.model';
import { RoleService } from '../../role/services/role.service';
import { Role } from '../../role/models/role.model';

@Injectable()
export class UserService extends AbstractService {

  /**
   * Firebase base key path.
   */
  protected readonly basePath: string = '/users';

  /**
   * Create new UserService instance.
   */
  public constructor(
    protected fb: AngularFireDatabase,
    protected roleService: RoleService,
  ) { super(); }

  public getList(): Observable<User[]> {
    return this.fb.list(this.basePath, {
      query: {
        limitToLast: 20
      }
    })
      .map((users: User[]) => User.fromJsonList(users))
      .map(users => users.map(user => user.refsObjectsToArrays()));
  }

  public getListWithRoles(): Observable<User[]> {
    return Observable.combineLatest(
      this.getList(),
      this.roleService.getList(),
    )
      .map(([users, roles]: [User[], Role[]]) => this.mergeRolesOnUsers(users, roles));
  }

  private mergeRolesOnUsers(users: User[], roles: Role[]) {
    return users.map(user => {
      roles.filter(role => user.role_ids.indexOf(role.$key) > -1)
        .map(role => user.roles.push(role));

      return user;
    }).reduce((prev, current) => prev.concat(current), []);
  }

  /**
   * Creates new user on storage.
   */
  public createNew(user: User): Observable<User> {
    user.refsArraysToObjects();
    const key = this.fb.app.database().ref().child(this.basePath).push(user).key;
    const data = {};
    data[this.basePath + '/' + key] = user;

    return this.fireBaseUpdate(data).catch(this.handleError);
  }

  public getUserById(userId: string): Observable<User> {
    return this.fb.object(this.basePath + '/' + userId);
  }

}



